*, *::before, *::after { padding: 0; margin: 0; box-sizing: border-box; position: relative; }
body { 
    max-width: 100%;
    min-height: 100vh; 
    background: linear-gradient(to right, #3f87a6, #ebf8e1); 
    font-family: 'Indie Flower', cursive; 
    line-height: 1.3; 
    padding: 1.75rem; 
    margin-left: auto;
    margin-right: auto;
}
h1 { 
    font-size: 3.5rem; 
    text-align: center; 
    text-shadow: 2px 1px 5px #fff; 
}
.players-turn {  
    font-size: 2.5rem;
    text-align: center;
    margin-top: 20px;
}
.btn { 
    display: block; 
    color: #000; 
    font-family: 'Indie Flower', cursive; 
    font-weight: 700;  
    outline: 0; 
    cursor: pointer; 
    &__player {
        font-size: 2rem;
        text-shadow: 2px 1px 5px #fff; 
        background: transparent;
        border: none;
        &:hover {
            text-decoration: underline;
        }
    }
    &__restart {
        font-size: 2rem; 
        padding: 12px 30px 8px; 
        margin: 30px auto; 
        border: 2px solid #000; 
        border-radius: 10px; 
        background: #fff;
    }
}
.gamemode {
    display: flex;
    justify-content: space-between;
    max-width: 480px;
    margin: 20px auto 40px;
    &__text {
        font-size: 2.5rem;
        text-align: center;
    }
}